






















import Vue from "vue";
import { Component, Prop, Watch } from "vue-property-decorator";

@Component
export default class NumberPad extends Vue {
  @Prop({ default: 0, type: Number }) readonly value!: number;
  @Prop(Boolean) readonly reset!: boolean;
  @Watch("reset")
  onFuckChange() {
      this.output = "0";
  }

  output = this.value.toString();
  inputContent(event: MouseEvent) {
    const button = event.target as HTMLButtonElement; //强行声明是非空按钮元素
    const input = button.textContent;

    if (this.output === "0" && input) {
      //初始状态
      if (input !== ".") {
        this.output = input;
      } else this.output += input;
    } else if (this.output.length > 0) {
      if (input === "." && this.output.indexOf(".") >= 0) {
        return;
      } //不许出现第二个小数点
      else this.output = this.output + input;
    }
  }
  remove() {
    if (this.output.length > 1) {
      this.output = this.output.slice(0, -1);
    } else this.output = "0";
  }
  clear() {
    this.output = "0";
  }

  ok() {
    if (this.output === "0" || this.output === "0.") {
      return;
    } else {
      const number = parseFloat(this.output);
      this.$emit("update:value", number);
      this.$emit("submit", number, this.output);
    }
  }
}
